import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { openSnackbar } from 'reducers/snackbar/action-creators'
import { getContent, filterContent, filterContentSuccess, deleteContent } from 'reducers/firestore/action-creators'
import { openForm } from 'reducers/content-form/action-creators'
import { connect } from 'react-redux'

import 'typeface-roboto' // AVISO! Adicionei essa biblioteca para fontes nos Headers!
import Paper from '@material-ui/core/Paper'
import { unstable_Box as Box } from '@material-ui/core/Box'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Grid from '@material-ui/core/Grid'
import FormGroup from '@material-ui/core/FormGroup'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'
import Radio from '@material-ui/core/Radio'
import SearchIcon from '@material-ui/icons/Search'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab'
import Typography from '@material-ui/core/Typography'
import BackIcon from '@material-ui/icons/ArrowBack'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import CurriculumData from '../CurriculumData'

import styles from './styles'
import { withStyles } from '@material-ui/styles'

import imgurUtils from 'utils/imgur'
import { compose, renderComponent } from 'recompose'

import { firebase } from '../../' // Importando firebase aqui!
import { RadioGroup } from '@material-ui/core';
import Firebase, {getResume} from '../../services/firebase'

class CurriculumPage extends React.Component {


    constructor(props) {
        super(props)

        const {
            openSnackbar,
            classes,
        } = props

        this.state = {
            openSnackbar,
            classes,
            rows: [],
            willShowData: true,
            // Atributos de estado relacionados ao formulário
            techcourse: false,
            profexperience: false, 
            cid: "",
            resumes: [],
        }

    }

    /**
     * Método para transformar os dados de entrada em um dicionário.
     */
    addCellData = (name, techcourse, profexperience, cid,
        address, city, phone, email, info) => {
        return {name, techcourse, profexperience, cid, address,
            city, phone, email, info}
    }

    /**
     * Método para reiniciar todos os dados resultantes da busca.
     */

    resetData = async () => {
        const snapshot = await firebase.firestore.collection("resumes").get();
        let response = snapshot.docs.map(item => ({id: item.id, data: item.data()})).filter(item => item.data.valid);

        if(this.state.profexperience) {
           response = response.map(item => {
            if(item.data.qualification && item.data.qualification.length) {
                return item;
            }
           });
        }

        if(this.state.techcourse) {
            response = response.map(item => {
            if(item.data.education && item.data.education.length) {
                return item;
            }
            });
        }

        if(this.state.cid) {
            response = response.map(item => {
                if(item.data.cid && item.data.cid.includes(this.state.cid)) {
                    return item;
                }
            });
        }

        console.log(JSON.stringify(response));

        this.setState({resumes: response});
        
        // Resetando a variável auxiliar 'willShowData' para true,
        // ou seja, será colocado em estado que receberá informações novos
        // do banco de dados.
        this.setState({willShowData: true})

        // Resetando todas as informações pré-existentes de busca anteriores.
        this.state.rows.forEach(element => this.state.rows.pop(element));

        // Mostrando o formulário de busca novamente!
        this.props.changeFormState();
    }

    /**
     * Método para gerenciar os checkboxes relacionados a gênero.
     */
    handleGenderCheckbox = name => (event) => {
        if (event.target.checked) {
            this.setState({gender: String(event.target.value).toLowerCase()});
        }
        else {
            for (let i = 0; i < this.state.gender.length; i++) {
                if (this.state.gender[i] == (event.target.value).toLowerCase()) {
                    this.state.gender.splice(i, 1)
                    break
                }
            }

        }
    }

    /**
     * Método para gerenciar os checkboxes relacionados a Maior Grau de Educação.
     */
    handleHighestGradeCheckbox = id => (event) => {
        if (event.target.checked) {
            this.state.highestgrade.push(id)
        }
        else {

            for (let i = 0; i < this.state.highestgrade.length; i++) {
                if (this.state.highestgrade[i] == id) {
                    this.state.highestgrade.splice(i, 1)
                    break
                }
            }

        }

    }

    /**
     * Método para gerenciar os checkboxes relacionados a Ensino Profissionalizante (Curso Técnico).
     */
    handleTechCourseCheckbox = value => (event) => {
        if(event.target.checked) {
            this.setState({techcourse: true});
        };
    }

    /**
     * Método para gerenciar os checkboxes relacionados a Experiência Profissional.
     */
    handleProfExperienceCheckbox = value => (event) => {

        if (event.target.checked) {
            this.setState({profexperience: true});
        }
    }

    /**
     * Método para gerenciar o texto do Cid10.
     */
    handleCidTextfield = value => (event) => {
        this.setState({cid: event.target.value})
    }

    onFormatQualification = (qualification) => {
        const company = qualification && qualification[0] && qualification[0].company ? `Empresa: ${qualification[0].company}` : '';
        const position =  qualification && qualification[0] && qualification[0].position ? `Cargo/Atividade: ${qualification[0].position}` : '';
        
        if(!company && !position) return 'sem experiência profissional';
        return `${company} | ${position}`;
    }

    onFormatEducation = (education) => {
        const institution = education && education[0] && education[0].institution ? `Instituição: ${education[0].institution}` : '';
        const course =  education && education[0] && education[0].course ? `Curso: ${education[0].course}` : '';

        if(!course && !institution) return 'sem experiência educacional';
        return `${institution} | ${course}`;
    }
    /**
     * Método auxiliar para filtrar queries do Firestore de forma
     * dinâmica, ou seja, com parâmetro de buscas diferentes.
     * 
     * ATENÇÃO! Esse método não é o mais otimizado para o servidor devido
     * a falta de operadores lógicos para a busca no Firestore! É recomendável
     * tentar buscar outro meio de filtro!!!
     */
    filterResult(doc) {
        let canShow = true

        if (!this.state.gender.includes(doc.data().gender.toLowerCase()) && this.state.gender.length > 0) {
            canShow = false
        }
        else if (!this.state.highestgrade.includes(parseInt(doc.data().highestgrade)) && this.state.highestgrade.length > 0) {
            canShow = false
        }
        else if (!this.state.profexperience.includes(doc.data().profexperience) && this.state.profexperience.length > 0) {
            canShow = false
        }
        else if (!this.state.techcourse.includes(doc.data().techcourse) && this.state.techcourse.length > 0) {
            canShow = false
        }
        else if (this.state.cid != "") {

            if (doc.data().cid == "" || doc.data().cid == null) {
                canShow = false
            }
            else if (this.state.cid.toLowerCase() != doc.data().cid.toLowerCase()) {
                canShow = false
            }
        }

        return canShow
    }

    /**
     * Método para carregar todas as informações do Firebase's Firestore
     * de acordo com a preferência do usuário.
     */
    renderData() {

        // Lembrando que se showResult (mudou para tela de resultados)
        // e willShowData (o banco de dados vai mostrar dados)... Os
        // dados deverão ser tratados!
        if (this.props.showResult && this.state.willShowData) {

            // Se há algo armazenado no "buffer" rows... Limpando tudo...
            if (this.state.rows.length > 0) {
                this.setState({rows: []})
            }

            firebase.firestore.collection("accounts").where("role", "==", "student-enabled").get().then((queryResults) => {

                let rows = []

                queryResults.forEach((doc) => {
                    if (this.filterResult(doc)) {

                        let toConvertData = doc.data()

                        // Convertendo Experiência Profissional de booleano para string
                        if (toConvertData.profexperience == true) {
                            toConvertData.profexperience = "Sim"
                        } 
                        else {
                            toConvertData.profexperience = "Não"
                        }
        
                        // Convertendo Ensino Técnico de booleano para string
                        if (toConvertData.techcourse == true) {
                            toConvertData.techcourse = "Sim"
                        } 
                        else {
                            toConvertData.techcourse = "Não"
                        }

                        // Tratando informação sobre o CID vazias...
                        if (toConvertData.cid == "" || toConvertData.cid == null) {
                            toConvertData.cid = "?"
                        }
                        
                        rows.push(this.addCellData(toConvertData.name,
                            toConvertData.techcourse, toConvertData.profexperience, toConvertData.cid, "?"))
        
                    }
                })

                // Limpando todos os parâmetros de busca...
                // E também diremos que não precisaremos obter mais nada do banco de dados
                this.setState({techcourse: [], profexperience: [], cid: "", willShowData: false, rows: rows})

            })
            .catch(err => {
                console.log("[ERROR] Database is not available or the connection was refused!")
            })
        }

    }

    render() {

        // Chamando método de validação de conteúdo.
        // Caso seja necessário buscar novas informações no banco de dados...
        // Os dados serão reamostrados na tela.
        this.renderData()

        // Renderizando componente...
        return (
            <Paper 
                className={this.state.classes.root}
                component={Box}
                margin='auto'
                maxWidth={1920}
            >
                <AppBar
                    component={Box}
                    borderBottom='1px solid rgba(0, 0, 0, 0.12)'
                    position='static'
                    color='default'
                    maxWidth={1920}
                    elevation={0}
                    padding={2}>

                    <Grid
                        alignItems='left'
                        container>

                        {/* TÍTULO - Buscar Curriculum */}
                        <Grid item xs={6}>
                            <Typography color='textPrimary' align='left' variant='h4' component='h4'>
                                Buscar Curriculum
                            </Typography>
                        </Grid>

                        {/* Tweakzinho para ajustar o botão de Nova Consulta no GRID */}
                        {this.props.showResult? (
                        <Grid item xs={4}></Grid>) : null}

                        {/* Botão de Nova Consulta */}
                        {this.props.showResult? (
                        <Grid item xs={2}>
                            <Box mt={1}>
                                <Button
                                    className={this.state.classes.button}
                                    color='primary'
                                    variant='contained'
                                    onClick={this.props.changeFormState}
                                >
                                    Nova consulta
                                </Button>
                            </Box>
                        </Grid>) : null}

                    </Grid>
                </AppBar>


                {this.props.showResult? 
                    (
                        /* Demonstrando uma lista de resultados do Banco de Dados */
                        <Grid
                            alignItems='left'
                            container
                            direction="column">
                            <Box padding={2}>

                                {/* Table View */}
                                <Grid item>
                                    <Table>
                                        <TableHead>
                                            <TableRow>
                                                <TableCell>Nome</TableCell>
                                                <TableCell>Ensino Profissionalizante</TableCell>
                                                <TableCell>Experiência Profissional</TableCell>
                                                <TableCell>CID</TableCell>
                                            </TableRow>
                                        </TableHead>

                                        <TableBody>
                                            {this.state.resumes && this.state.resumes[0] && (
                                            this.state.resumes.map(item => (
                                                <CurriculumData name={(item.data.name)} 
                                                    profexperience={this.onFormatQualification(item.data && item.data.qualification)}
                                                    techcourse={this.onFormatEducation(item.data && item.data.education)}
                                                    cid={item.data.cid ? item.data.cid : 'nao especificada'} />
                                            )))}
                                        </TableBody>
                                    </Table>
                                </Grid>
                            </Box>
                        </Grid>
                    ) : 
                    (
                        /* Demonstrando um formulário de busca */
                    
                    <Grid
                        alignItems='left'
                        container
                        direction="column">

                        <Box padding={2}>
                            {/* Ensino Profissionalizante */}
                            <Grid item>
                                <Box mt={5}>
                                    <FormControl component="fieldset">
                                        <Box mb={2}>
                                            <Typography color='textPrimary' align='left' variant='h4' component='h4'>
                                                Ensino Profissionalizante:
                                            </Typography>
                                        </Box>
                                        <Box ml={1}>
                                            <FormGroup>
                                                <FormControlLabel
                                                    control={<Checkbox value="Sim" onChange={this.handleTechCourseCheckbox(true)} />}
                                                    label="Sim"
                                                    />
                                                <FormControlLabel
                                                    control={<Checkbox value="Não" onChange={this.handleTechCourseCheckbox(false)} />}
                                                    label="Não"
                                                    />
                                            </FormGroup>
                                        </Box>
                                    </FormControl>
                                </Box>
                            </Grid>

                            {/* Experiência Profissional */}
                            <Grid item>
                                <Box mt={5}>
                                    <FormControl component="fieldset">
                                        <Box mb={2}>
                                            <Typography color='textPrimary' align='left' variant='h4' component='h4'>
                                                Experiência Profissional:
                                            </Typography>
                                        </Box>
                                        <Box ml={1}>
                                            <FormGroup>
                                                <FormControlLabel
                                                    control={<Checkbox value="Sim" onChange={this.handleProfExperienceCheckbox(true)} />}
                                                    label="Sim"
                                                    />
                                                <FormControlLabel
                                                    control={<Checkbox value="Não" onChange={this.handleProfExperienceCheckbox(false)} />}
                                                    label="Não"
                                                    />
                                            </FormGroup>
                                        </Box>
                                    </FormControl>
                                </Box>
                            </Grid>

                            {/* CID */}
                            <Grid item>
                                <Box mt={5}>
                                    <FormControl component="fieldset">
                                        <Box mb={2}>
                                            <Typography color='textPrimary' align='left' variant='h4' component='h4'>
                                                CID (Opcional):
                                            </Typography>
                                            <TextField
                                                label="Digite o CID aqui"
                                                margin="normal"
                                                fullWidth="true"
                                                onChange={this.handleCidTextfield()}
                                            />
                                        </Box>
                                    </FormControl>
                                </Box>
                            </Grid>

                            {/* Botão de busca */}
                            <Grid item>

                                <Box mt={5} mb={1}>
                                    <Button
                                        className={this.state.classes.button}
                                        color='primary'
                                        variant='contained'
                                        fullWidth='true'
                                        onClick={() => this.resetData()}
                                    >
                                        Buscar
                                    </Button>
                                </Box>

                            </Grid>
                        </Box>
                    </Grid>
                )}
            </Paper>
        )
    }

}

const mapStateToProps = ({ firestore }) => ({ firestore })

const mapDispatchToProps = dispatch => ({
  getContent: type => dispatch(getContent({ type })),
  openSnackbar: options => dispatch(openSnackbar(options)),
  filterContent: query => dispatch(filterContent(query)),
  filterContentSuccess: () => dispatch(filterContentSuccess()),
  deleteContent: (uid, options) => dispatch(deleteContent(uid, options)),
  openForm: options => dispatch(openForm(options)),
})

export default compose(
    withStyles(styles),
    connect(mapStateToProps, mapDispatchToProps),
)(CurriculumPage);